import { useEffect, useState } from 'react';
import './App.css';
import queryBackend from './backend/BackendQueryEngine';
import modelChoiceComponent from './components/modelChoiceComponent';
import { DataCountArray } from './types/DataArray';
import Visualization from "./Visualization";

function App() {

  const [exampleData, setExampleData] = useState<DataCountArray>();
  const [modelChoice, setModelChoice] = useState<Boolean>();
  const [dataIsLoading, setDataIsLoading ] = useState<Boolean>();
  
  useEffect(() => {
    setDataIsLoading(true);
    queryBackend(`get-data?name=` + modelChoice).then((exampleData) => {
      setExampleData(exampleData);
      setDataIsLoading(false);
    });
  }, [modelChoice]);
  
  console.log('Example data', exampleData) 
  console.log('Data choice', modelChoice)
  console.log('DATA IS LOADING', dataIsLoading)

  return (
    <div className="App">
      <header className="App-header"> MIcrosoft News Dataset (MIND)
      </header>
      <div>
      <modelChoiceComponent onChoiceMade={setModelChoice}/>
      </div>
      {dataIsLoading && <p>Loading...</p>}
      <div>{exampleData && modelChoice && <Visualization width={1100} height={550} data={exampleData} />}</div>
    </div>
  )
}

export default App;
