import React from 'react';

interface Option {
  id: number,
  name: string,
}
class modelChoiceComponent extends React.Component<{onChoiceMade: any}, {text: string}>{
  // TODO: type this properly
  datasetOptions: Array<Option>;

  constructor(props: any) {
    super(props);
    this.state = {text: ''};
    this.handleChange = this.handleChange.bind(this);
    this.handleChoice = this.handleChoice.bind(this);
    this.datasetOptions = [
      {
        id : 1,
        name : 'news',
      },
      {
        id : 2,
        name : 'behaviors',
      },
    ]
  }

  render() {
    return (
      <div>
        <form>
          <label htmlFor="data-choice">
            Which dataset do you want to display? (news, behaviors)
          </label>
          <input
            id="data-choice"
            onChange={this.handleChange}
            value={this.state.text}
          />
          <button onClick={this.handleChoice}>
            Confirm 
          </button>
        </form>
      </div>
    );
  }

  handleChange(e : any) {
    this.setState({ text: e.target.value});
  }

  handleChoice(e : any){
    console.log('Previous state: ', this.state)
    this.props.onChoiceMade(this.state.text);
    this.setState({
      text: '',
    });            
  }
}
export default modelChoiceComponent;
